.bock-item-style-1 {
    display: block;
    position: relative;
    .item__img {
        position: relative;
        &:after {
            content: "";
            position: absolute;
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            mix-blend-mode: multiply;
            transition: 0.3s opacity ease-in-out;
        }
    }
    .item__desc {
        position: absolute;
        &:after {
            content: "";
            width: 100%;
            height: 100%;
            background: rgba($color: #2f2f2f, $alpha: 0.8);
            mix-blend-mode: multiply;
            background-blend-mode: multiply;
            position: absolute;
            top: 0;
            left: 0;
        }
        .item__title,
        .item__subtitle {
            position: relative;
            z-index: 10;
        }
        .item__title {
            font-family: SVN--Bold;
        }
    }
}

.isImage {
    cursor: pointer;
    position: relative;
    &::before {
        content: "";
        position: absolute;
        width: 1.8rem;
        height: 1.8rem;
        top: 1.4rem;
        left: 2.4rem;
        background: url(../assets/images/gallery/image/image.png) no-repeat;
        background-size: 100% 100%;
        z-index: 1;
    }
}

.isVideo {
    position: relative;
    &::before {
        content: "";
        position: absolute;
        width: 2.8rem;
        height: 1.8rem;
        top: 1.4rem;
        left: 2.4rem;
        background: url(../assets/images/gallery/image/video.png) no-repeat;
        background-size: 100% 100%;
        z-index: 1;
    }
    &:after {
        content: "";
        cursor: pointer;
        user-select: none;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        background-image: url(../assets/icons/btn-play.svg);
        background-size: 100% 100%;
        width: 52px;
        height: 52px;
        opacity: 0.65;
        box-shadow: 3px 3px 10px rgba($color: #000000, $alpha: 0.44);
    }
}