    .index-about {
        position: relative;
        padding-top: 190px;
        overflow: hidden;
        &:after {
            content: "";
            position: absolute;
            width: 952px;
            height: 478px;
            top: 0;
            left: 0;
            background: #212121;
            z-index: -1;
            box-shadow: 0 3px 6px rgba($color: #000000, $alpha: 0.2);
            @media (max-width: 1699.98px) {
                width: 45%;
                height: 50%;
            }
        }
        @media (max-width: 1439.98px) {
            padding-top: 100px;
            &:after {
                content: none;
            }
        }
        @media (max-width: 1024.98px) {
            padding-top: 50px;
        }
        @media (max-width: 575.98px) {
            padding-top: 25px;
        }
    }
    
    .index-about-wrapper {
        position: relative;
        @media (max-width: 1024.98px) {
            display: flex;
            flex-wrap: wrap;
        }
    }
    
    .slider-index-about {
        position: absolute;
        left: 0;
        top: 50%;
        transform: translateY(-50%);
        max-width: 57.3%;
        width: 100%;
        .img {
            transition: 0.5s clip-path linear, 0.35s opacity linear;
            opacity: 0.4;
            clip-path: inset(15% 0 15% 0);
        }
        .swiper {
            &-container {
                padding-left: 278px;
                @media (max-width: 1599.98px) {
                    padding-left: 25%;
                }
                @media (max-width: 1024.98px) {
                    padding-left: 0;
                }
            }
            &-slide-active {
                .img {
                    clip-path: inset(0 0 0 0);
                    opacity: 1;
                    box-shadow: 0 3px 10px rgba($color: #000000, $alpha: 0.38);
                }
            }
        }
        @media (max-width: 1024.98px) {
            position: static;
            top: unset;
            left: unset;
            transform: unset;
            // NEW LAYOYT
            -ms-flex: 0 0 100%;
            flex: 0 0 100%;
            max-width: 100%;
            order: 2;
            margin-top: 50px;
        }
    }
    
    .index-about-description {
        .description-wrapper {
            max-width: 35.9%;
            margin-left: auto;
            @media (max-width: 1024.98px) {
                max-width: 100%;
            }
        }
        .main-title {
            margin-bottom: 10px;
            h2 {
                &:before {
                    top: -12.2vw;
                    left: -2px;
                }
                &:after {
                    right: 0;
                    top: -6vw;
                }
                @media (max-width: 1599.98px) {
                    &:before,
                    &:after {
                        font-size: 11vw;
                    }
                }
                @media (max-width: 1439.98px) {
                    &:before,
                    &:after {
                        font-size: 10vw;
                    }
                    &:before {
                        top: -3vw;
                    }
                    &:after {
                        top: 4vw;
                    }
                }
                @media (max-width: 1024px) {
                    display: inline-block;
                    padding-top: 5%;
                    padding-right: 5%;
                }
                @media (max-width: 575.98px) {
                    padding-top: 10%;
                    padding-right: 10%;
                    &:before,
                    &:after {
                        font-size: 8rem;
                    }
                    &:after {
                        top: 7vw;
                    }
                }
            }
            @media (max-width: 1024.98px) {
                margin-bottom: 50px;
            }
            @media (max-width: 575.98px) {
                margin-bottom: 25px;
            }
        }
        .main-title--small {
            // @media (max-width: 1024.98px) {
            //     height: 54px;
            // }
            @media (max-width: 575.98px) {
                height: unset;
            }
        }
        .list-item-description {
            @media (max-width: 1024px) {
                display: flex;
                flex-wrap: wrap;
                margin-right: -15px;
                margin-left: -15px;
            }
        }
        .item-description+.item-description {
            margin-top: 18px;
            @media (max-width: 1024px) {
                margin-top: 0;
            }
            @media (max-width: 575.98px) {
                margin-top: 25px;
            }
        }
        .item-description {
            @media (max-width: 1024.98px) {
                -ms-flex: 0 0 100%;
                -webkit-box-flex: 0;
                flex: 0 0 50%;
                max-width: 50%;
                padding-right: 15px;
                padding-left: 15px;
                margin-bottom: 20px;
            }
            @media (max-width: 575.98px) {
                -ms-flex: 0 0 100%;
                -webkit-box-flex: 0;
                flex: 0 0 100%;
                max-width: 100%;
            }
        }
        .description {
            max-width: 400px;
            width: 100%;
            margin-top: 18px;
            padding-left: 16px;
            p {
                font-size: 1.7rem;
            }
            ul {
                list-style-type: none;
                padding-left: 0;
            }
            li {
                position: relative;
                padding-left: 0px;
                font-size: 17px;
                &:before {
                    content: "";
                    width: 5px;
                    height: 5px;
                    position: absolute;
                    left: -10px;
                    top: 8px;
                    background: map-get($color, x-white);
                    border-radius: 50%;
                }
            }
            li+li {
                margin-top: 5px;
            }
            @media (max-width: 575.98px) {
                margin-top: 15px;
            }
        }
        @media (max-width: 1024.98px) {
            -ms-flex: 0 0 100%;
            -webkit-box-flex: 0;
            flex: 0 0 100%;
            max-width: 100%;
            order: 1;
        }
    }
    
    .index-core-values {
