        @media (max-width: 1199.98px) {
            margin-right: -35px;
            margin-left: -35px;
        }
        @media (max-width: 1024.98px) {
            margin-right: -10px;
            margin-left: -10px;
        }
        @media (max-width: 575.98px) {
