    .categories-service-wrapper {
        margin-top: 30px;
        .category-service__name {
            cursor: pointer;
            user-select: none;
            font-size: 2.5rem;
            display: flex;
            justify-content: center;
            align-items: center;
            height: 66px;
            border: 2px solid rgba($color: #eae2d0, $alpha: 0.14);
            overflow-y: auto;
            padding: 10px;
            transition: background 0.35s ease-in-out, border 1s linear;
            &::-webkit-scrollbar {
                display: none;
            }
            @media (min-width: 1025px) {
                &:hover {
                    background: #212121;
                    border: 2px solid rgba($color: #707070, $alpha: 1);
                }
            }
            @media (max-width: 767.98px) {
                height: 45px;
                font-size: 2rem;
            }
            @media (max-width: 575.98px) {
                height: 66px;
                font-size: 1.7rem;
            }
        }
    }
    
    .slider-index-service {
        position: relative;
        margin: 70px 0 50px;
        width: 100%;
        .bock-item-style-1 {
            transform: scale(0.5);
            transition: 0.5s 0.5s transform ease-in-out;
            .item__img {
                &:after {
                    background: rgba($color: #2f2f2f, $alpha: 0.6);
                }
            }
            .item__desc {
                bottom: 5%;
                left: 0;
                width: 70%;
                padding: 10px 15px;
                transition: 0.35s left linear;
                &:after {
                    background: rgba($color: #2f2f2f, $alpha: 0.8);
                }
                .item__title {
                    font-size: 2rem;
                    @media (max-width: 1024.98px) {
                        font-size: 1.8rem;
                    }
                    @media (max-width: 575.98px) {
                        font-size: 1.6rem;
                    }
                }
                .item__subtitle {
                    font-size: 1.7rem;
                    @media (max-width: 575.98px) {
                        font-size: 1.4rem;
                    }
                }
                @media (max-width: 575.98px) {
                    width: 100%;
                }
            }
            @media (max-width: 1024.98px) {
                transform: scale(0.8);
            }
            @media (max-width: 575.98px) {
                transform: scale(1);
                .item__desc {
                    opacity: 0;
                    transition: 0.35s 0.5s all ease-in-out;
                }
            }
        }
        .swiper {
            &-container {
                padding: 0 25%;
                @media (max-width: 767.98px) {
                    padding: 0 10%;
                }
                @media (max-width: 575.98px) {
                    padding: 0;
                }
            }
            &-slide-prev {
                .item__desc {
                    left: 10%;
                }
            }
            &-slide-active {
                .bock-item-style-1 {
                    transform: scale(1);
                }
                .item__img {
                    &:after {
                        opacity: 0;
                    }
                }
                .item__desc {
                    opacity: 1;
                }
            }
        }
        .index-service-prev-slider,
        .index-service-next-slider {
            position: absolute;
            bottom: 0;
            z-index: 10;
        }
        .index-service-prev-slider {
            left: 19%;
            @media (max-width: 1359.98px) {
                left: 40%;
            }
            @media (max-width: 575.98px) {
                left: 20%;
            }
        }
        .index-service-next-slider {
            right: 19%;
            @media (max-width: 1439.98px) {
                right: 40%;
            }
            @media (max-width: 575.98px) {
                right: 20%;
            }
        }
        @media (max-width: 1359.98px) {
            padding-bottom: 35px;
        }
        @media (max-width: 575.98px) {
            margin: 35px 0;
        }
    }
    
