    .slider-index-projects-review {
        position: relative;
        padding: 80px 0;
        @media (max-width: 575.98px) {
            padding: 0 0;
        }
        &:after {
            content: "";
            position: absolute;
            top: 50%;
            right: 0;
            width: 10%;
            height: 85%;
            background: rgba($color: #212121, $alpha: 1);
            z-index: -1;
            transform: translateY(-50%);
            @media (max-width: 1024px) {
                content: none;
            }
        }
        &:before {
            content: "";
            position: absolute;
            top: 50%;
            left: 0;
            width: 10%;
            height: 85%;
            transform: translateY(-50%);
            background: rgba($color: #212121, $alpha: 1);
            z-index: -1;
            @media (max-width: 1024px) {
                content: none;
            }
        }
        .swiper {
            &-slide {
                .bock-item-style-1 {
                    @media (min-width: 1600px) {
                        height: 390px;
                    }
                    @media (max-width: 1599.98px) {
                        height: 330px;
                    }
                    @media (max-width: 1440.98px) {
                        height: 525px;
                    }
                    @media (max-width: 1024.98px) {
                        height: 430px;
                    }
                    @media (max-width: 768.98px) {
                        height: 340px;
                    }
                }
                .item__img {
                    height: 100%;
                }
            }
            &-slide-active {
                .bock-item-style-1 {
                    transform: scale(1);
                    box-shadow: 3px 3px 10px rgba($color: #000000, $alpha: 0.35);
                    &:after {
                        opacity: 0;
                    }
                    .item__img {
                        height: 100%;
                    }
                    .item__desc {
                        opacity: 1;
                        .item__title,
                        .item__subtitle {
                            color: #eae2d0;
                        }
                    }
                }
            }
            &-slide-prev {
                .bock-item-style-1 {
                    @media (min-width: 1441px) {
                        transform-origin: left;
                        transition: 1.5s transform-origin cubic-bezier(0.4, 0, 1, 1);
                    }
                }
            }
            &-slide-next {
                .bock-item-style-1 {
                    @media (min-width: 1441px) {
                        transform-origin: right;
                        transition: 1.5s transform-origin cubic-bezier(0.4, 0, 1, 1);
                    }
                }
            }
        }
        .bock-item-style-1 {
            position: relative;
            transform: scale(0.7);
            transition: 1.5s transform cubic-bezier(0.4, 0, 1, 1);
            margin-bottom: 10px;
            .item__desc {
                width: 80%;
                left: 0;
                bottom: 35px;
                padding: 25px 50px;
                opacity: 0;
                .item__title {
                    color: map-get($color, x-white);
                    font-size: 2rem;
                    margin-bottom: 5px;
                    @media (max-width: 575.98px) {
                        font-size: 1.6rem;
                    }
                }
                .item__subtitle {
                    color: map-get($color, x-white);
                    font-size: 1.7rem;
                    @media (max-width: 575.98px) {
                        font-size: 1.4rem;
                    }
                }
            }
            &:after {
                content: "";
                position: absolute;
                width: 100%;
                height: 100%;
                top: 0;
                left: 0;
                background: rgba($color: #2f2f2f, $alpha: 0.65);
                mix-blend-mode: multiply;
                pointer-events: none;
                transition: 0.35s opacity ease-in-out;
            }
            @media (max-width: 1024.98px) {
                transform: scale(.8);
                .item__desc {
                    padding: 10px 20px;
                }
            }
        }
        @media (max-width: 1024.98px) {
            padding-bottom: 0;
        }
    }
    
