        .index-news-prev-slider,
        .index-news-next-slider {
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            z-index: 10;
        }
        .index-news-prev-slider {
            left: 45%;
            @media (max-width: 1024.98px) {
                left: 40%;
            }
            @media (max-width: 575.98px) {
                left: 10%;
            }
        }
        .index-news-next-slider {
            right: 45%;
            @media (max-width: 1024.98px) {
                right: 40%;
            }
            @media (max-width: 575.98px) {
                right: 60%;
            }
        }
    }
    
    .slider-index-news-wrapper {
        position: relative;
        padding: 100px 0;
        margin-top: 75px;
        &:after {
            content: "";
            position: absolute;
            top: 0;
            bottom: 0;
            width: 25%;
            background: #212121;
            z-index: -1;
        }
        @media (max-width: 575.98px) {
            padding: 0;
        }
    }
    
    .slider-index-news {
        clip-path: inset(-100vw 0 -100vw -100vw);
        .bock-item-style-1 {
            height: 460px;
            @media (max-width: 575.98px) {
                height: 300px;
            }
            transform: scale(0.8);
            transition: 0.3s transform ease-in-out;
            .item__img {
                height: 100%;
                @media (max-width: 575.98px) {
                    max-height: 300px;
                }
            }
            .item__desc {
                padding: 30px 30px 20px 30px;
                bottom: 0;
                width: 100%;
                .item__title,
                .item__subtitle {
                    color: map-get($color, x-white);
                }
                .item__title {
                    font-size: 2.5rem;
                    margin-bottom: 10px;
                    @media (max-width: 575.98px) {
                        font-size: 1.8rem;
                    }
                }
                .item__subtitle {
                    font-size: 1.7rem;
                    @media (max-width: 575.98px) {
                        font-size: 1.4rem;
                    }
                }
                @media (max-width: 575.98px) {
                    padding: 15px;
                }
            }
            // @media (max-width: 1024.98px) {
            //     .item__desc {
            //         opacity: 0;
            //         transition: 0.35s opacity ease-in-out;
            //     }
            //     .item__img {
            //         &:after {
            //             background: rgba($color: #2f2f2f, $alpha: 0.85);
            //             transition: 0.35s opacity ease-in-out;
            //         }
            //     }
            // }
            @media (max-width: 575.98px) {
                .item__desc {
                    opacity: 1;
                }
            }
        }
        .swiper {
            &-slide {
                &:hover {
                    .bock-item-style-1 {
                        transform: scale(1);
                        transition: .4s transform ease-in-out;
                    }
                    @media (max-width: 1024.98px) {
                        .bock-item-style-1 {
                            .item__desc {
                                opacity: 1;
                            }
                            .item__img {
                                &:after {
                                    opacity: 0;
                                }
                            }
                        }
                    }
                }
            }
            &-slide-duplicate-prev {
                .bock-item-style-1 {
                    .item__desc {
                        opacity: 0;
                    }
                }
            }
        }
    }
    
