.galleries-image__wrapper {
	.main-title {
		margin-bottom: 4rem;
	}
	.pagination {
		margin-bottom: 9rem;
		@media (max-width: 575.98px) {
			margin-bottom: 0;
		}
	}
}

.gallery--tab {
	padding: 0;
	display: flex;
	justify-content: center;
	list-style-type: none;
	margin-bottom: 7.6rem;
	.item {
		max-width: 34.8rem;
		border: 1px solid #707070;
		height: 6.6rem;
		width: 100%;
		a {
			text-align: center;
			display: flex;
			width: 100%;
			height: 100%;
			justify-content: center;
			align-items: center;
			font-size: 2.5rem;
			font-family: SVN--Light;
			color: map-get($color, main-2);
			transition: .5s all ease-in-out;
			@media (max-width: 575.98px) {
				font-size: 2rem;
			}
		}
		&.item+.item {
			margin-left: 2rem;
		}
		&.active {
			background: #212121;
		}
		@media (min-width: 1025px) {
			&:hover {
				transition: .5s all ease-in-out;
				background: #212121;
			}
		}
	}
	@media (max-width: 575.98px) {
		margin-bottom: 3.6rem;
		.item {
			height: 4.6rem;
		}
	}
}

.gallery--content {
	margin-bottom: 5rem;
	@media (max-width: 575.98px) {
		margin-bottom: 2rem;
	}
}

.list-gallery {
	.item-image {
		cursor: pointer;
		position: relative;
		overflow: hidden;
		margin-bottom: 2rem;
		.desc-image {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			width: 27rem;
			color: map-get($color, x-white);
			height: 155px;
			text-align: center;
			background: rgba($color: #000000, $alpha: .6);
			display: flex;
			flex-direction: column;
			justify-content: center;
			align-items: center;
			transition: .5s all ease-in-out;
			opacity: 0;
			h2 {
				font-size: 2.5rem;
				font-family: SVN--Bold;
			}
			p {
				font-size: 1.7rem;
				font-family: SVN--Light;
				-webkit-box-orient: vertical;
				display: -webkit-box;
				overflow: hidden;
				text-overflow: ellipsis;
				-webkit-line-clamp: 1;
			}
			@media (max-width: 1024px) {
				top: 50%;
			}
		}
		&::before {
			content: "";
			position: absolute;
			width: 1.8rem;
			height: 1.8rem;
			top: 1.4rem;
			left: 2.4rem;
			background: url(../assets/images/gallery/image/image.png) no-repeat;
			background-size: 100% 100%;
		}
		&.custom {
			.desc-image {
				top: 50%;
			}
			&::before {
				content: none;
			}
			@media (min-width: 1025px) {
				&:hover {
					.desc-image {
						transition: .5s all ease-in-out;
						top: 50%;
					}
					.button-next,
					.button-prev {
						transition: .5s all ease-in-out;
						opacity: 0;
					}
				}
			}
		}
		@media (min-width: 1025px) {
			&:hover {
				.desc-image {
					opacity: 1;
					transition: 1s all ease-in-out;
				}
				.button-next,
				.button-prev {
					opacity: 1;
					transition: 1s all ease-in-out;
				}
			}
		}
		@media (max-width: 767.98px) {
			.desc-image {
				width: 24rem;
				height: 100px;
			}
		}
	}
}

#pop-up--image {
	display: inline-block !important;
	background: #0A0A0A;
	height: 100%;
	transition: 1s all ease-in-out;
	padding: 0;
	.slider-image-gallery {
		margin: 0;
		height: 100%;
		display: flex;
		align-items: center;
		.slider-image-gallery__wrapper {
			height: 40%;
			width: 100%;
		}
		.swiper-slide {
			.bock-item-style-1 {
				transform: scale(.7);
				transition: 1s all ease-in-out;
				&::after {
					transition: 1s all ease-in-out;
					content: "";
					mix-blend-mode: multiply;
					position: absolute;
					background: #2F2F2F;
					opacity: .85;
					top: 0;
					left: 0;
					width: 100%;
					height: 100%;
				}
				.item__img {
					&::after {
						content: none;
					}
					&::before {
						content: "";
						position: absolute;
						width: 1.8rem;
						height: 1.8rem;
						top: 1.4rem;
						left: 2.4rem;
						background: url(../assets/images/gallery/image/image.png) no-repeat;
						background-size: 100% 100%;
					}
				}
			}
		}
		.swiper-slide-active {
			.bock-item-style-1 {
				transform: scale(1);
				&::after {
					transition: 1s all ease-in-out;
					opacity: 0 !important;
					
				}
				transition: 1s all ease-in-out;
				// .item__img {
				// 	height: 100%;
				// }
			}
		}
		
		.desc {
			margin-top: 1.2rem;
			width: 33.33%;
			margin: 0 auto;
			position: relative;
			.list-button-relate-news {
				justify-content: center;
				display: flex;
				width: 100%;
				margin: 0;
				padding: 0;
				margin-top: 10px;
				
				// .relate-next-slider {
				// 	position: absolute;
				// 	top: 0;
				// 	right: 0;
				// 	padding: 0;
				// }
				// .relate-prev-slider {
				// 	position: absolute;
				// 	top: 0;
				// 	left: 0;
				// 	padding: 0;
				// }
			}
			.desc-content {
				text-align: center;
				color: map-get($color, x-white);
				margin: 0 7rem;
				h2 {
					font-size: 2.5rem;
					font-family: SVN--Bold;
					position: relative;
					&::before {
						position: absolute;
						content: "";
						top: 50%;
						left: 0;
						transform: translateY(-50%);
						background: #707070;
						height: 1px;
						width: 8rem;
					}
					&::after {
						position: absolute;
						content: "";
						top: 50%;
						right: 0;
						transform: translateY(-50%);
						background: #707070;
						height: 1px;
						width: 8rem;
					}
				}
				p {
					text-align: justify;
					text-align-last: center;
					font-size: 1.7rem;
					font-family: SVN--Light;
				}
			}
			@media (max-width: 1440px) {
				width: 50%;
			}
			@media (max-width: 843.98px) {
				.desc-content {
					h2 {
						&::before,
						&::after {
							width: 4rem;
						}
					}
				}
			}
			@media (max-width: 767.98px) {
				width: 80%;
			}
			@media (max-width: 499.98px) {
				width: 100%;
			}
		}
	}
}

.fancybox-button.fancybox-close-small {
	background: #212121;
}

.fancybox-bg {
	background: #0A0A0A !important;
	opacity: 1 !important;
}

.fancybox-slide--html {
	padding: 0 !important;
}