.tab-container {
	.list-tab {
		.item {
			cursor: pointer;

			&.active {
				color: map-get($color, main-1);
			}
		}
	}

	.tab-content {
		.content {
			display: none;
			opacity: 0;
			transition: all 0.3s cubic-bezier(0.2, 0.5, 0.5, 0.8);

			&.show {
				opacity: 1;
			}
		}
	}
}
